import React, {Component} from 'react';
import './App.css';
import Timer from './components/Timer/Timer';


class App extends Component{

  state = {
    names: [
      'Vic',
      'Vivian',
      'Vicki',
      'Venessa',
      'Forbes',
      'Vincent'
    ]
  }


  render(){
    const length = this.state.names.length - 1
    const randomNumber = parseInt(Math.random() * length)
    return (
      <div className="App">
        <header className="App-header">
          <h1>Hello...{this.state.names[randomNumber]}</h1>
          <p>You have this much time left...</p>
          <Timer/>
        </header>
      </div>
    );
  }
}

export default App;
